import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import RepositoryOwnerInput from './RepositoryOwnerInput';
import RepositorySelector from './RepositorySelector';
import BranchSelector from './BranchSelector';
import CommitSelector from './CommitSelector';
import BenchmarkCharts from './BenchmarkCharts';
import { fetchBenchmarkData } from '../../utils/api';

const BenchmarkDisplay = () => {
  const [repoOwner, setRepoOwner] = useState('');
  const [repoName, setRepoName] = useState('');
  const [selectedBranch, setSelectedBranch] = useState('');
  const [commitHash, setCommitHash] = useState('');
  const [benchmarkData, setBenchmarkData] = useState({});
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleSubmitCommitHash = async (e) => {
    e.preventDefault();
    if (repoOwner && repoName && selectedBranch && commitHash) {
      setLoading(true);
      setError(null);
      try {
        const data = await fetchBenchmarkData(repoOwner, repoName, commitHash);
        setBenchmarkData(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    } else {
      setError('Please fill in all fields');
    }
  };

  return (
    <Container fluid className="p-4">
      <h1 className="h2 mb-4">Benchmark Results</h1>
      <Form onSubmit={handleSubmitCommitHash}>
        <Row className="mb-3">
          <Col xs={12} md={3}>
            <Form.Label>Repository Owner</Form.Label>
            <RepositoryOwnerInput
              repoOwner={repoOwner}
              setRepoOwner={setRepoOwner}
            />
          </Col>
          <Col xs={12} md={3}>
            <Form.Label>Repository Name</Form.Label>
            <RepositorySelector
              repoOwner={repoOwner}
              repoName={repoName}
              setRepoName={setRepoName}
            />
          </Col>
          <Col xs={12} md={3}>
            <Form.Label>Branch</Form.Label>
            <BranchSelector
              repoOwner={repoOwner}
              repoName={repoName}
              selectedBranch={selectedBranch}
              setSelectedBranch={setSelectedBranch}
            />
          </Col>
          <Col xs={12} md={3}>
            <Form.Label>Commit Hash</Form.Label>
            <CommitSelector
              repoOwner={repoOwner}
              repoName={repoName}
              selectedBranch={selectedBranch}
              commitHash={commitHash}
              setCommitHash={setCommitHash}
            />
          </Col>
        </Row>
        <Row>
          <Col xs={12} className="text-center">
            <Button type="submit" variant="primary" className="px-5" disabled={!commitHash}>
              Fetch Benchmark Data
            </Button>
          </Col>
        </Row>
      </Form>
      {loading && <p className="alert alert-info mt-3">Loading...</p>}
      {error && <p className="alert alert-danger mt-3">Error: {error}</p>}
      <BenchmarkCharts benchmarkData={benchmarkData} />
    </Container>
  );
};

export default BenchmarkDisplay;